import Vue from "vue"
import Vuex from "vuex"
import App from "./components/App.vue"
import { store } from "./store.js"

Vue.use(Vuex);

let app = new Vue({
	store,
	el: "#app",
	render: h => h(App),
	mounted: function() {
		store.dispatch("setPlaces");
	},
});



